export default () => {

    //  cookie   name,  ,  ,  undefined
    function getCookie(name) {
        var matches = document.cookie.match(new RegExp(
            "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
        ));
        return matches ? decodeURIComponent(matches[1]) : undefined;
    }

    //  cookie   name   value
    // options -    cookie (expires, path, domain, secure)
    function setCookie(name, value, options) {
        options = options || {path: '/'};

        var expires = options.expires;

        if (typeof expires == "number" && expires) {
        } else {
            expires = 60 * 60 * 24 * 365;
        }
        var d = new Date();
        d.setTime(d.getTime() + expires * 1000);
        expires = options.expires = d;
        if (expires && expires.toUTCString) {
            options.expires = expires.toUTCString();
        }

        value = encodeURIComponent(value);

        var updatedCookie = name + "=" + value;

        for (var propName in options) {
            updatedCookie += "; " + propName;
            var propValue = options[propName];
            if (propValue !== true) {
                updatedCookie += "=" + propValue;
            }
        }

        document.cookie = updatedCookie;
    }

    //  cookie   name
    function deleteCookie(name) {
        setCookie(name, "", {
            expires: -1,
            path: '/'
        })
    }

    const popup = document.querySelector('.js--cookie-popup'),
        btn = document.querySelector('.js--cookie-success');

    if (!popup) return;

    const cookie = getCookie('AcceptCookieNew');

    if (cookie !== 'yes') popup.classList.add('_active');

    btn.addEventListener('click', (e) => {
        e.preventDefault();
        setCookie('AcceptCookieNew', 'yes');
        popup.classList.remove('_active');
    });

}